import { h } from 'preact';
import { useContext } from 'preact/hooks';
import { Router, route } from 'preact-router';
import AsyncRoute from 'preact-async-route';
import '../scripts/base';

// Code-splitting is automated for `routes` directory
import Welcome_Spinner from '../components/Welcome_Spinner';
import { AuthContext } from '../components/Auth';


const App = () => {
  const { currentUser } = useContext(AuthContext);

  const handleRoute = async (event) => {
    if (currentUser && event.url === '/login') {
      //console.log("[Redirecting] redirect to home page...");
      route('/index.html');
      return;
    }

    if (!currentUser && (event.url === '/menu' || event.url === '/menus')) {
      //console.log("[Redirecting] redirect to login page...");
      route('/login');
      return;
    }
  };

  return (
    <div id="app">
      <Router onChange={handleRoute}>
        <AsyncRoute
          path="/login"
          getComponent={() => import('../routes/login').then(module => module.default)}
          loading={() => <Welcome_Spinner />}
        />
        <AsyncRoute
          path="/index.html"
          getComponent={() => import('../routes/buy').then(module => module.default)}
          loading={() => <Welcome_Spinner />}
        />
        <AsyncRoute
          path="/menu"
          getComponent={() => import('../routes/menu').then(module => module.default)}
          loading={() => <Welcome_Spinner />}
        />
        <AsyncRoute
          path="/menus"
          getComponent={() => import('../routes/menus').then(module => module.default)}
          loading={() => <Welcome_Spinner />}
        />
        <AsyncRoute
          path="/validate"
          getComponent={() => import('../routes/validate').then(module => module.default)}
          loading={() => <Welcome_Spinner />}
        />
      </Router>
    </div>
  );
}

export default App;
