import { createContext } from 'preact';
import { useEffect, useState } from 'preact/hooks';
import { route } from 'preact-router';
import { getAuth, onAuthStateChanged, signOut } from '../scripts/firebase-auth';

import Welcome_Spinner from './Welcome_Spinner';
import Offline from './Offline';
import Warning from './Warning';
import '../scripts/base';

export const AuthContext = createContext({});

export const AuthProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [online, setOnline] = useState(navigator.onLine);
  const [warning, setWarning] = useState(false);
  const [deviceId, setDeviceId] = useState('');
  const auth = getAuth();

  useEffect(() => {
    //console.log('%cRUN AGAIN', 'color:orange; background:black; padding: 5px;');
    window.chrome.tabs.onUpdated.addListener((tabId, changeInfo, tab) => {
      if (tab.active && !(/\/admin\/menus*/.test(tab.url) || /\/store\/.*\/menus*/.test(tab.url))) {
        window.close();
      }

      if (!navigator.onLine) {
        window.close();
      }
    });
  }, []);

  useEffect(() => {
    window.addEventListener('online', () => {
      //console.log('%cONLINE AGAIN', 'color:orange; background:black; padding: 5px;');
      setOnline(true);

      return window.chrome.tabs.query({ active: true, currentWindow: true })
        .then((tabObj) => {
          const [tab] = tabObj;

          const { url } = tab;
          //console.log(url);

          const regex = /admin\/menus\/[0-9]+(\?.*)?$/;
          const regex1 = /store\/.*\/menus\/[0-9]+(\?.*)?$/;
          const regex2 = /admin\/menus\/?(\?.*)?$/;
          const regex3 = /store\/.*\/menus\/?(\?.*)?$/;

          if (regex.test(url) || regex1.test(url)) {
            route('/menu');
            return;
          } else if (regex2.test(url) || regex3.test(url)) {
            route('/menus');
            return;
          }
        });
    });

    window.addEventListener('offline', () => {
      //console.log('%cOFFLINE AGAIN', 'color:orange; background:black; padding: 5px;');
      setOnline(false);
    });
  }, []);

  useEffect(() => {
    return onAuthStateChanged(auth, (user) => {
      //console.log("[INFO] onAuthStateChanged Run!!! User:", user);
      setCurrentUser(user);
      if (user) {
        return user.getIdTokenResult()
          .then((id_token_result) => {
            //console.log('id_token_result.claims:', id_token_result.claims);
            return id_token_result.claims.master_device_id
          })
          .then((deviceId) => {
            setDeviceId(deviceId);
            setLoading(false);
          })
          .catch((error) => {
            //console.log(error);
          });
      }

      route('/index.html');
      setLoading(false);
    });
  });

  useEffect(() => {
    if (currentUser && deviceId) {

      return getActiveTab()
        .then((tab) => {
          window.chrome.tabs.sendMessage(
            tab.id,
            { type: 'setCookie' },
            async (response) => {
              //console.log(tab.id, response);
              //console.log('%csendMessage Returned:', 'color:blue;background:red;padding:5px;', tab.id, response);
              const cookies = await window.chrome.cookies.getAll({ domain: 'shopify.com' });
              const { value } = cookies.find(cookie => cookie.name === 'master_device_id');

              if (deviceId && value) {
                if (deviceId !== value) {
                  // Diff device, revoke license key.
                  //console.log('%cDiff device, Show Warning!.', 'color:blue;background:red;padding:5px;', deviceId, value);
                  setWarning(true);

                  const auth = getAuth();
                  signOut(auth).then(() => {
                    // Sign-out successful.
                  }).catch((error) => {
                    // An error happened.
                  });

                } else {
                  // Same device do not revoke license key.
                  //console.log('%cSame device do not Show Warning!.', 'color:white;background:black;padding:5px;', deviceId, value);
                  setWarning(false)
                }
              }
            }
          )
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }, [currentUser, deviceId])

  const getActiveTab = async () => {
    const queryOptions = { active: true, currentWindow: true };
    const [tab] = await window.chrome.tabs.query(queryOptions);

    return tab;
  }

  return (
    <AuthContext.Provider value={{ currentUser, loading, setLoading, setWarning }}>
      {
        !online ? <Offline /> :
          loading ? <Welcome_Spinner /> : warning ? <Warning /> : children
      }
    </AuthContext.Provider>
  );
};